import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import classNames from 'classnames';
import gsap from 'gsap';

import { request } from '../actions'

import OpeningContainer from '../components/OpeningContainer/OpeningContainer';
import RankingContainer from '../components/RankingContainer/RankingContainer';
import PickupWordContainer from '../components/PickupWordContainer/PickupWordContainer';
import EndingContainer from '../components/EndingContainer/EndingContainer';

class SnippetsContainer extends Component {

  static propTypes = {
    data: PropTypes.object,
    request: PropTypes.func.isRequired,
  }

  static defaultProps = {
    data: {}
  }

  constructor(props) {
    super(props);

    // this.searchInputRef = React.createRef();
    // this.titleInputRef = React.createRef();
    // this.textInputRef = React.createRef();
    // this.handleRef = React.createRef();

    // this.isInitAssigned = false;
    this.state = {
      actIndex: 0,
      isDataLoaded: false
      // isEditingText: false,
      // isOpenSetting: false,
      // tmpSnippetFileLocation: "",
      // selectIndex: 0,
      // searchText: "",
      // items: [],
    };
  }

  componentDidMount() {
    this.props.request()
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    // console.log(this.props.data)
    const { data } = this.props;
    const { actIndex, isDataLoaded } = this.state;

    if (isDataLoaded === false && data != undefined) {
      this.act();
      this.setState({ isDataLoaded: true });
    }

    if (prevState.actIndex != actIndex) this.act()
  }

  act() {
    // this.setState({ actIndex: 2 })
    // return;
    const { actIndex } = this.state;
    switch (actIndex) {
      case 0:
        // window.BGCanvasSetColor(0.0, 0.0, 1.0)
        gsap.delayedCall(1.0, () => {
          this.setState({ actIndex: 1 })
          window.BGCanvasFadeIn()
        });
        break;

      case 1:
        gsap.delayedCall(6.0, () => {
          this.setState({ actIndex: 2 })
        });
        break;

      case 2:
        gsap.delayedCall(20.0, () => {
          window.BGCanvasToneDown()
          this.setState({ actIndex: 3 })
        });
        break;

      case 3:
        gsap.delayedCall(45.0, () => {
          this.setState({ actIndex: 4 })
        });
        break;

      case 4:
        gsap.delayedCall(45.0, () => {
          this.setState({ actIndex: 5 })
        });
        break;

      case 5:
        gsap.delayedCall(45.0, () => {
          this.setState({ actIndex: 6 })
          window.BGCanvasToneUp()
        });
        break;

      default:
        break;
    }
  }

  render() {
    // const { items, selectIndex, isOpenSetting, tmpSnippetFileLocation } = this.state;
    const { actIndex } = this.state;

    // items.forEach((v, index) => {

    //   const title = v.title;
    //   const tmpTitles = [];
    //   let newTitle = "";
    //   const tags = [];

    //   title.split(' ').forEach((t, i) => {
    //     if (t.length != 1 && t.slice(0, 1).indexOf(":") != -1) {
    //       tags.push(t.slice(1));
    //     } else {
    //       tmpTitles.push(t);
    //     }
    //   });


    //   newTitle = tmpTitles.join(" ");

    //   // make tag list
    //   let tagElements = [];
    //   tags.forEach(v => {
    //     const colHex = "#" + convert.hsl.hex(seedrandom(v)() * 120 + 100, 60, 40);
    //     tagElements.push(<span className="tags" style={{ backgroundColor: colHex }} key={v}>{v}</span>);
    //     // tagElements.push(<span className="tags"  key={v}>{v}</span>);
    //   })

    //   list.push(<div
    //     key={index}
    //     className={"item " + (this.state.selectIndex ==== index ? "slected" : "")}
    //     onMouseDown={() => {
    //       this.setState({ selectIndex: index });
    //     }}
    //   >
    //     {tagElements}
    //     {newTitle === "" ? "new snippet" : newTitle}
    //   </div>);
    // });

    return (
      <div className='containers'>
        <OpeningContainer {...this.props} className={classNames('container-common')} isShow={actIndex === 1} />
        <RankingContainer {...this.props} className={classNames('container-common')} isShow={actIndex === 2} />
        <PickupWordContainer {...this.props} rankIndex={0} className={classNames('container-common')} isShow={actIndex === 3} />
        <PickupWordContainer {...this.props} rankIndex={1} className={classNames('container-common')} isShow={actIndex === 4} />
        <PickupWordContainer {...this.props} rankIndex={2} className={classNames('container-common')} isShow={actIndex === 5} />
        <EndingContainer {...this.props} className={classNames('container-common')} isShow={actIndex === 6} />
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return ({
    data: state.data.data
  });
}

export default connect(mapStateToProps,
  {
    request
  }
)(SnippetsContainer)